import React, { useState, useEffect } from 'react';

export default function NameInput() {
  const [name, setName] = useState(JSON.parse(window.localStorage.getItem('username')) || '');
  const [currName, setCurrName] = useState(name);
  const [editing, setEditing] = useState(name === '');

  const handleInputChange = (e) => {
    setCurrName(e.target.value);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setName(currName);
    window.localStorage.setItem('username', JSON.stringify(currName));
    setEditing(false);
  };

  const handleOpenForm = () => {
    setEditing(true);
  };

  return (
    <div style={{ width: '40%', zIndex: 1, marginBottom: 100 }}>
      {editing === true ? (
        <>
          <h1
            style={{
              color: '#efeeee',
              textAlign: 'center',
              fontWeight: 300,
              fontSize: '3rem',
              marginBottom: 40,
              userSelect: 'none',
            }}
          >
            What's your name?
          </h1>
          <form onSubmit={handleSubmit}>
            <input
              type="text"
              size="50"
              maxLength="255"
              autoComplete="off"
              placeholder="Enter your name..."
              value={currName}
              onChange={handleInputChange}
              onFocus={(e) => {
                e.target.select();
              }}
              autoFocus
            />
          </form>
        </>
      ) : (
        <h1
          style={{
            color: '#efeeee',
            textAlign: 'center',
            fontWeight: 300,
            fontSize: '3rem',
            marginBottom: 40,
            cursor: 'pointer',
            userSelect: 'none',
          }}
          onClick={handleOpenForm}
        >
          Hello, {name}!
        </h1>
      )}
    </div>
  );
}
